.bubble-wrapper {
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 0; 
  filter: url('#goo');
  overflow: hidden;
}

.safari {
  filter: blur(20px);
}


.bubble {
  position: absolute;
  height: 7vh;
  width: 7vh;
  border-radius: 50%;
  background: linear-gradient(250deg, var(--purple), var(--orange));
  /* box-shadow: 0px 5px 13px -3px #000000; */
  cursor: pointer;
  animation: animate 1s linear forwards;
}


.bubble:hover {
  background: linear-gradient(250deg, white, var(--orange));
}